    monospace;
}

.banner-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    background-color: #A7C1CF;
    width: 100%;
}

a {
    margin-left: 20px;
